import React from "react";
import { Link } from 'gatsby';
import '../styles/styles.sass';
import Nav from '../components/Nav';
import Tag from '../components/Tag';

const SingleTagTemplate = ({data, pageContext}) => {
  const { posts, tagName } = pageContext;

  return (
    <section className="section" style={{"paddingTop": "0rem"}}>
      
      <div className="container">
        <div className="columns">
          <div className="column is-one-fourth"> </div>
          <div className="column">  
            <Nav />
            <p>
              <br />
              <p style={{marginBottom: "0.5rem"}}><strong className="has-text-grey">All Posts About</strong> <Tag name={tagName} /></p>
              <ul>
                {posts.map((post, index) => {
                  return (
                    <div className="box" key={post.frontmatter.path} style={{color: "grey", marginBottom: "1rem"}}>
                      <div class="card-image">
                      <figure class="image is-16by9">
                        <img src={post.frontmatter.img} alt="Placeholder image"></img>
                      </figure>
                      </div>
                      <span style={{fontSize: "0.75rem"}}>{post.frontmatter.date} </span> <br />
                      <Link to={post.frontmatter.path}>
                      <p>{post.frontmatter.title}</p>
                      </Link>
                      <p style={{fontSize: "0.85rem", marginBottom: "0.5rem", marginTop: "0.1rem"}}>{post.frontmatter.excerpt}</p>
                      {post.frontmatter.tags.map(tag => <Tag name={tag} />)} 
                    </div>
                  )
                })}
              </ul>
            </p>
          </div>
          <div className="column"></div>
        </div>
      </div>
    </section>
  )

  
}

export default SingleTagTemplate;